.Sidebar {
  padding: 1rem;
  background-color: hsla(45,95%,95%,1);
  box-shadow: inset 0 0 2rem hsla(45,50%,80%,1);
  border-right: 1px #fff solid;
  box-sizing: border-box;
  width: 80%;
  max-width: 20rem;
  flex: none;
}

.Home {
  margin: 2rem 3rem 2rem 4rem;
}

@media only screen and (max-width: 767px) {
.Home {
  margin: 2rem 2rem 2rem 3rem;
}
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Header {
  padding: 1rem 0;
  background-color: #fff;
  margin-bottom: 1rem;
}

.Header .App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  width: 4rem;
  height: 3rem;
  cursor: pointer;
}

.Header .icon {
  margin: 0.7rem 0 0 0;
  opacity: 0.5;
  font-size: 1.5rem;
  cursor: pointer;
}

.Header .green.icon,
.Header .red.icon,
.Header .blue.icon,
.Header .yellow.icon {
  opacity: 1;
  cursor: default;
}

.Header .blue.icon {
  cursor: progress;
}

@media only screen and (max-width: 767px) {
.Header .wrapper {
  margin-left: 0!important;
}
}

.Footer {
  color: rgba(0,0,0,0.5);
  font-size: 0.85rem;
}

.Footer a {
  margin: 0 0.5rem;
  opacity: 0.7;
  display: inline-block;
}

.Footer .wrapper {
  margin: 0 auto;
  padding: 0 4rem 2rem 4rem; 
}

@media only screen and (max-width: 767px) {
.Footer .wrapper {
  padding-left: 0;
  padding-right: 0;
}
}

.Menu-wrapper {
  flex: none;
  width: 4rem;
}

.Menu-wrapper .MenuItem,
.Menu-wrapper .MenuYearMark {
  padding-left: 0!important;
  text-align: right!important;
  font-family: monospace;
}

.Menu-wrapper .MenuItem .Time {
  font-size: 0.85em;
  opacity: 0.7;
  margin-top: 0.25em;
  display: block;
}

.Menu-wrapper .MenuYearMark {
  font-weight: bold!important;
  padding-top: 0.5em!important;
  padding-bottom: 0.5em!important;
  background-color: #d4d4d5!important;
}

.Menu-wrapper .MenuItem.in-viewport:not(.active),
.Menu-wrapper .MenuYearMark.in-viewport {
  border-color: hsla(0,0%,0%,0.3)!important;
}

.Menu-wrapper .MenuItem.not-staged:not(.active) {
  padding: 0!important;
  margin-top: 0.5rem!important;
  margin-bottom: 0.5rem!important;
  border-top: 2px #d4d4d5 solid!important;
  height: 0;
  color: transparent!important;
  -webkit-transition: margin 0.5s ease-out!important;
  transition: margin 0.5s ease-out!important;
  -webkit-transition: padding 0.5s ease-out!important;
  transition: padding 0.5s ease-out!important;
}

.Menu-wrapper .MenuYearMark.not-staged:not(.active) {
  padding-top: 0!important;
  padding-bottom: 0!important;
  -webkit-transition: margin 0.5s ease-out!important;
  transition: margin 0.5s ease-out!important;
  -webkit-transition: padding 0.5s ease-out!important;
  transition: padding 0.5s ease-out!important;
}

.Event-wrapper {
  flex-grow: 1;
  padding-left: 2rem;
}

.Event-wrapper .Filter-message {
  color: rgba(0,0,0,0.7);
  font-size: 0.85rem;
  text-align: center;
}

.Event-wrapper .Timestamp {
  color: rgba(0,0,0,0.5);
  font-size: 0.7rem;
  font-family: monospace;
  text-align: right;
  position: absolute;
  top: 0;
  right: 1rem;
  cursor: pointer;
}

.Event-wrapper .Event-block.active {
  border: 2px #e03997 solid;
  margin: -1px!important;
}

.Event-wrapper .Event-block .description {
  margin-top: -0.5em;
  color: rgba(0,0,0,0.7);
  font-size: 0.85rem;
}

.Event-wrapper .Event-block a {
  opacity: 0.85;
}

.Event-wrapper .Event-block .ui.horizontal.label {
  margin: 0 0.3em 0.3em;
  color: inherit;
}

.Event-wrapper .Event-block .ui.horizontal.label:first-child {
  margin-left: 0;
}

.Event-wrapper .Event-block .ui.horizontal.label:last-child {
  margin-right: 0;
}

.Event-wrapper .Event-block blockquote {
  margin: 1rem 1.5rem;
  position: relative;
  color: rgba(0,0,0,0.7);
}

.Event-wrapper .Event-block blockquote:first-child {
}

.Event-wrapper .Event-block blockquote:last-child {
  margin-bottom: 0;
}

.Event-wrapper .Event-block blockquote .Author {
  font-size: 0.85rem;
  color: rgba(0,0,0,0.5);
  text-align: right;
  margin-right: -1rem;
  margin-top: 1em;
}

.Event-wrapper .Event-block blockquote i.icon.quote {
  position: absolute;
  opacity: 0.3;
}

.Event-wrapper .Event-block blockquote i.icon.left.quote {
  left: -1.5em;
}

.Event-wrapper .Event-block blockquote i.icon.right.quote {
  right: -1.5em;
}

.Event-wrapper .Note .horizontal.divider>.icon {
  opacity: 0.3;
  font-size: 0.75rem;
}

.Event-wrapper .Note-content {
  color: rgba(0,0,0,0.7);
  margin-top: 0.5rem;
}

@media only screen and (max-width: 767px) {
.Event-wrapper {
  padding-left: 0;
}
.Event-wrapper article:last-child .grid .five.wide.column {
  margin-bottom: -1rem!important;
}
.Event-wrapper .Note .horizontal.divider {
  display: none;
}
.Event-wrapper .Note-content {
  margin-top: -1rem;
  margin-bottom: 1rem;
  margin-left: 1.5rem;
  position: relative;
}
.Event-wrapper .Note-content:before {
  content: '\F0F4';
  font-family: Icons;
  font-size: 0.75rem;
  opacity: 0.3;
  position: absolute;
  left: -1.5rem;
  top: 0;
}

}

.App {
  display: flex;
  position: relative;
  left: 0;
  margin-left: -20rem;
  min-height: 100vh;
}

.App .App-name {
  font-family: 'Jacques Francois Shadow', cursive;
}

.App .App-description {
  font-family: 'Arvo', serif;
}

.App .App-main {
  flex-grow: 1;
}

.Body-wrapper {
  padding-bottom: 2rem;
}

.Body-wrapper .Body {
  display: flex;
}


@media only screen and (max-width: 767px) {
html, body {
  font-size: 14px;
}
.Body {
  margin: 0 -1rem;
}
}

html, body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  background-color: #eee;
  color: rgba(0,0,0,0.85);
  min-height: 100vh;
}

